body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background-color: #f8f9fa;
}

.news-wrapper {
  max-width: 900px;
  margin: 0 auto;
}

#hero {
  height: 300px;
  background-color: #545454;
  border-bottom: 5px solid;
}

.news-item {
  background: #fff;
  padding: 20px 25px;
  border-radius: 25px;
  margin-bottom: 20px;
  box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
}
.news-item h2 {
  font-size: 1.4rem;
  margin-bottom: 10px;
}
.news-item h2 a {
  text-decoration: none;
  color: rgba(13, 109, 253, 0.7137254902);
}
.news-item h2 a:hover {
  text-decoration: underline;
}
.news-item .date {
  font-size: 0.9rem;
  color: #6c757d;
}

#load-more {
  border-radius: 30px;
  font-weight: 500;
}

footer a {
  display: inline-block;
}
footer img {
  margin-top: 15px;
  width: 40px;
  filter: brightness(0) invert(1);
}